import React, { Component } from 'react';

class TaskItem extends Component {
  onUpdateStatus = () => {
      this.props.onUpdateStatus(this.props.task.id);
  }

  onDelete = () => {
      this.props.onDelete(this.props.task.id);
  }
   onUpdate = () => {
    this.props.onUpdate(this.props.task.id)
   }


  render() {
    var {task, index} = this.props //var task = this.props.task
    return (
        <tr>
           <td>{index + 1}</td>
           <td>{task.name}</td>
           <td className="text-center">
              <span className={task.status === true ? 'btn-status label label-danger' : 'btn-status label label-success'}
                    onClick = {this.onUpdateStatus}
              >{task.status === true? 'Kích hoạt' : 'Ẩn'}</span>
            </td>
           <td className="text-center button-group">
                <button type="button"
                        className="btn btn-warning"
                        onClick = {this.onUpdate}
                >
                  <span className="fa fa-pencil mr-5"></span>Sửa
                </button>
               <button type="button" 
                        className="btn btn-danger"
                        onClick = {this.onDelete}
                >
                  <span className="fa fa-trash mr-5"></span>Xóa
                </button>
           </td>
        </tr>
    );
  }
}

export default TaskItem;



